@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

body {
  margin: 0;
  font-family: 'Lobster', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Lobster', cursive;
}

.mainheader {
	width: 50%;
	margin: 0px auto;
	display: block;
	border: 2px solid;
	position: relative;
	border-radius: 5px;
	padding: 2%;
	text-align: center;
}

.main-header-app {
	float: left;
	width: 95%;
	border: 4px double #000;
	margin: 7px;
}

.main-order-app {
		float: left;
		width: 32%;
		border: 4px double #000;
		margin: 4px;
		border-radius: 7px;
}
.main-inventory-app {
	float: left;
	width: 32%;
	border: 4px double #000;
	margin: 4px;
	border-radius: 7px;
}
.fish-menu-main {
	float: left;
	width: 32%;
	border: 4px double #000;
	margin: 4px;
	border-radius: 7px;
}
.logo {
	font-size: 3em;
	float: left;
	text-align: center;
	font-weight: bold;
	width: 100%;
}
.tagline {
	text-align: center;
	float: left;
	width: 100%;
	font-weight: 500;
}
.menu-item li {
	display: inline;
	padding: 1%;
	text-align: center;
	margin: 2px;
}
.menu-item {
	display: block;
	width: 100%;
	text-align: center;
	float: left;
	list-style: none;
	margin: 0px;
	padding: 0px;
}
.main-StorePicker-app {
	width: 50%;
	margin: 2% auto;
	text-align: center;
	/* align-content: space-around; */
	border: 2px dotted #000;
	padding: 2%;
}
.fish-edit-form input {
	border: 1px solid #000 !important;
	padding: 1%;
	border-radius: 6%;
	margin: 3%;
	box-shadow: 3px 3px 6px #000;
}
.fish-desc{
	border: 1px solid #000 !important;
	width: 92%;
	padding: 1%;
	border-radius: 4%;
	margin: 3%;
	box-shadow: 3px 3px 6px #000;

}
.fish-edit-form {
	border: 1px solid;
	border-radius: 2%;
	margin: 2%;
}
.fish-name {
	width: 30%;
}
.fish-price{
	width: 30%;
}
.fish-selection{
	width: 20%;
}
.submit-button {
	width: 96%;
	margin: 2%;
	background: #000;
	color: #fff;
	border: 0px;
	padding: 1%;
	font-size: 16px;
}
.fish-image{
	width: 92%;
}
.fishmenu-list {
	width: 93%;
	padding: 2%;
	margin: 1%;
	list-style-type: none;
	border-bottom: 4px double #000;
	display: flex;
}
.image-fish {
	width: 150px;
	height: 150px;
	float: left;
	display:block;
}
.fish-main-image {
	width: 30%;
	display: block;
	float: left;
}
.fish-main-body {
	width: 100%;
	display: flex;
}
.fish-name-conatiner {
	width: 100%;
	text-align: left;
	font-weight: bold;
	font-size: 1em;
}
.price {
	float: right;
	display: block;
	font-size: 14px;
}
.submit-samples {
	width: 96%;
	margin: 2%;
	background: #000;
	color: #fff;
	border: 0px;
	padding: 1%;
	font-size: 16px;
}
.add-order {
	width: 96%;
	margin: 2%;
	background: #000;
	color: #fff;
	border: 0px;
	padding: 1%;
	font-size: 16px;
}
.image-fish img {
	width: 160px;
	height: 160px;
}
.main-order-app li {
	list-style: none;
	border-bottom: 2px solid;
	width: 96%;
	margin: 0 auto;
}
.order-fish-name {
	width: 50% !important;
	display: block;
	float: left;
}
.order-price-fish {
	width: 30% !important;
	text-align: right;
}
.order-price-fish {
	width: 50% !important;
	text-align: right !important;
	display: table;
}
.total-price-order {
	list-style: none !important;
	border: none !important;
}
.order-total {
	width: 50% !important;
	display: block;
	float: left;
}
.total-amount {
	width: 50% !important;
	text-align: right !important;
	display: table;

}
.fish-content {
	width: 62%;
	margin: 0% -1% 0% 13%;
	padding: 0%;
	text-align: justify;
	left: 2% !important;
}
.removebutton {
	background: #000;
	color: #fff;
	border: 0px;
	padding: 1% 2%;
	border-radius: 3px;
}
.main-StorePicker-app {
	margin: 20% auto;
	text-align: center;
	border: 2px solid #000;
	padding: 2%;
	background: linear-gradient(rgba(25, 20, 0, 0.5), rgba(255, 250, 255, 0.5));
	position: fixed;
	width: 100%;
}
.main-store-entry{
	position: absolute;
	width: 100%;
	height: 100vh;
	background-image: url('https://raventools.com/blog/wp-content/uploads/2013/03/cool-background-2-800x389.png'),linear-gradient(rgba(255, 20, 0, 0.5), rgba(255, 250, 255, 0.5));
	background-blend-mode: multiply;
	background-attachment: fixed;
	background-repeat:repeat ;
}

.main-body-dom {
	background-image: url('https://raventools.com/blog/wp-content/uploads/2013/03/cool-background-2-800x389.png');
	position: absolute;
	background-position: center center;
	background-attachment: fixed;
	width: 100%;
	height: 100%;
}
.main-body-app {
	background-image: url('https://raventools.com/blog/wp-content/uploads/2013/03/cool-background-2-800x389.png');
	position: absolute;
	background-position: center center;
	background-attachment: fixed;
	width: 100%;
}
.store-selector button {
	background: #000;
	color: #fff;
	padding: 1% 3%;
	margin: 1%;
	border: 0px;
}
.store-selector input {
	background: transparent;
	border: 0px;
	box-shadow: none;
	border-bottom: 1px solid #000;
	padding: 1% 7%;
}
.main-order-app h2 {
	text-align: center;
}
.main-inventory-app h2 {
	text-align: center;
}